





















import { showToast } from 'unit/index.js'
export default {
	data () {
		return {
			account: '',
			password: '',
		}
	},
	methods:{
		login(e) {
			this.fromRul()
		},
		register() {
			uni.navigateTo({
				url: '/pages/my/register/index',
			});
		},
		fromRul() {
			if (!this.account) {
				showToast('用户名不能为空')
				return
			}
			if (!this.password) {
				showToast('密码不能为空')
				return
			}
		}
	}
}



